import React from 'react';
import TextField from 'material-ui/TextField';

const Search = ({handleInput}) => {
    
    return (
        <TextField
            hintText="请输入城市名称"
            floatingLabelText="城市名称，请使用中文"
            onKeyPress={e=>e.key==='Enter'?handleInput(e.target.value):null}
        />
    );
};

export default Search;


