import React, { Component } from 'react';
import injectTapEventPlugin from 'react-tap-event-plugin';
injectTapEventPlugin();
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import Paper from 'material-ui/Paper';
import Search from './components/Search'
import Result from './components/Result'
import $ from 'jquery'
 
const paperStyle = {
  minHeight: 600,
  width: 360,
  margin: '20px auto',
  textAlign: 'center',
};

class App extends Component {
  constructor(){
    super()
    this.state={
      weatherInfo:[]
    }
  }

  handleInput(city){
    $.ajax({
      url:`http://api.map.baidu.com/telematics/v3/weather?location=${city}&output=json&ak=65c84f3654ea728597effe6c22e6de45`,
      dataType:'jsonp'  
    }).done(data =>{
      this.setState({
        weatherInfo:data.results
      })
    })
  }

  render() {
    return (
        <MuiThemeProvider>
          <div>
            <AppBar
              title="React Weather"
              showMenuIconButton={false}
            />
            <Paper style={paperStyle} zDepth={5}>
              <Search handleInput={this.handleInput.bind(this)}/>
              <Result weatherInfo={this.state.weatherInfo}/>
            </Paper>
          </div>
        </MuiThemeProvider>
    );
  }
}

export default App;



